import React, { useEffect } from "react"
import styled from "styled-components"
import PostTagListComp from "../PostList/PostTagListComp"
import Prism from "prismjs";

type PostType = {
  data : {
    post: {
      html: string
      frontmatter: {
        date: string
        decs: string
        postUrl: string
        tag: Array<string>
        title: string
        type: string
      }
    }
  }
}

const PostComp = ({data}: PostType) => {
    const {html, frontmatter : {type, title, tag, postUrl, decs, date}} = data.post;

    useEffect(() => {
      return Prism.highlightAll();
    });

    return (
      <ContentWrap>
        <PostWrap>
          <PostTitle>{title}</PostTitle>
          <PostDate>{date}</PostDate>
          <PostTagListComp tags={tag}/>
          <PostContent dangerouslySetInnerHTML={{ __html: html}}></PostContent>
        </PostWrap>
      </ContentWrap>
    );
}

export default PostComp;

const ContentWrap = styled.div`
  display: flex;
`;

const PostWrap = styled.div`
  margin-top: 100px;
  width: 740px;
`;

const PostTitle = styled.div`
  font-weight: bold;
  font-size: 2rem;
`;

const PostDate = styled.div`
  font-size:0.8rem;
  border-bottom: 1px solid #3399ff;
  padding: 10px 0;
`;

const PostContent = styled.div`
  margin-top:50px;
`;
